<template>
  <v-container fluid fill-height grid-list-md style="padding:0px 10px 5px 10px;height:100%;width:100%;background-color:white;">  
    <v-layout row>
      <v-flex d-flex md6>
        <v-layout column fill-height>
          <v-flex d-flex md3 style="max-height:25%;">             
            <TimelineChart
              v-if="chartData['pcomp']"
              v-bind:chartOptions="chartData['pcomp']"/>
          </v-flex>
          <v-flex d-flex md3 style="max-height:25%;">            
            <TimelineChart
              v-if="chartData['pmax']"
              v-bind:chartOptions="chartData['pmax']"/>
          </v-flex>
          <v-flex d-flex md3 style="max-height:25%;">            
            <TimelineChart
              v-if="chartData['tTurbIn']"
              v-bind:chartOptions="chartData['tTurbIn']"/>
          </v-flex>
          <v-flex d-flex md3 style="max-height:25%;">             
            <TimelineChart
              v-if="chartData['tcspeed']"
              v-bind:chartOptions="chartData['tcspeed']"/>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-flex d-flex md6>
        <v-layout column fill-height>
          <v-flex d-flex md3 style="max-height:25%;">            
            <TimelineChart/>
          </v-flex>
          <v-flex d-flex md3 style="max-height:25%;">            
            <TimelineChart/>
          </v-flex>
          <v-flex d-flex md3 style="max-height:25%;">             
            <TimelineChart/>
          </v-flex>
          <v-flex d-flex md3 style="max-height:25%;">             
            <TimelineChart/>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
  
</template>

<script>

import TimelineChart from  '../controls/TimelineChart'

import {globalStore} from "../main.js"

export default {
  name: 'EngineGraphs',
  components: {
    TimelineChart
  },
  data () {
    return {
      chartData:{}
    }
  }, 
  mounted()
  {
    let cylCount=6;
    let tcCount=1;

    let timeStamps = Object.keys(globalStore.data24h.sigs);

    let params=['pcomp', 'pmax','tcspeed', 'tscav','tTurbIn'];   
    let elCount=[cylCount, cylCount, tcCount,tcCount,tcCount];
    
    for (let p=0;p<params.length;p++)
    {
      this.$set(this.chartData,params[p],{
        legend:{
          enabled:false
        },
        title:{
            text:params[p]
        },
        series: [{                        
            type:'scatter',
            data:[]
        },
        {
            type:'arearange',                       
            data:[],
            marker:{
                enabled:false
            },
            color:'rgb(219, 219, 219)',
            fillOpacity: 0.6,
        }]
      });

      for (let i=0;i<timeStamps.length;i++)
      {
        // let arg = i;
        // let val = Math.random()*150;

        
        // let min = Math.random()*150;
        // let max = min + Math.random()*(150-min);


        let val = this.average(params[p], elCount[p],timeStamps[i]);

        
        console.log(timeStamps[i]+ " "+val);

        let color = 'rgb(60, 171, 48)';

        //if ((val<min)||(val>max)) color = 'rgb(205, 57, 64)';

        this.chartData[params[p]].series[0].data.push({
            x:i,
            y:val,
            color:color
        });

        //this.chartData[params[p]].rangeData.push([arg, min, max]);
      }    

    }

    // let tparams=[ 'tcspeed', 'tscav','tTurbIn'];

    // for (let tp=0;tp<tparams.length;tp++)
    // {
    //   let val = this.average(tparams[tp], tcCount);

    //   this.$set(this.cardData,tparams[tp],{
    //     value:val,
    //     ref:100,
    //     title:tparams[tp],
    //     unit:'unit',        
    //     format:2,
    //     color:0
    //   });
    // }

    // let params = ['pscav','soPresDispl','fRailPres'];

    // for (let p=0;p<params.length;p++)
    // {
    //   let val = globalStore.data15m.sigs[params[p]];

    //   this.$set(this.cardData,params[p],{
    //     value:val,
    //     ref:100,
    //     title:params[p],
    //     unit:'unit',        
    //     format:2,
    //     color:0
    //   });
    // }
    
  },
  methods: {
    average: function(param, count, timeStamp)
    {
      let sum =0;

      for (let i=1;i<=count;i++)
      {      
        sum+=globalStore.data24h.sigs[timeStamp][param+i];
      }

      return sum/count;
    }
  }
}
</script>

