<template>
  <v-container fluid fill-height grid-list-md style="padding:0px 10px 5px 10px;width:100%;height:100%;background-color:white;">
    <v-layout row>
      <v-flex d-flex md6>
        <v-layout column fill-height>
          <v-flex d-flex md3 style="max-height:25%;">
            <v-layout row>
                <v-flex d-flex md4>
                    <Card
                        v-bind:cardData="cardData['pcomp']"
                        v-bind:flat="false"/>
                </v-flex>
                <v-flex d-flex md4>
                    <Card
                        v-bind:cardData="cardData['pmax']"
                        v-bind:flat="false"/>
                </v-flex>
                <v-flex d-flex md4>
                    <Card
                        v-bind:cardData="cardData['pmaxPcomp']"
                        v-bind:flat="false"/>
                </v-flex>
            </v-layout>
          </v-flex>
          <v-flex d-flex md3 style="max-height:25%;">
            <BarChart
              v-if="chartData['pcomp']"
             v-bind:chartOptions="chartData['pcomp']"/>
          </v-flex>
          <v-flex d-flex md3 style="max-height:25%;">
            <BarChart
            v-if="chartData['pmax']"
             v-bind:chartOptions="chartData['pmax']"/>
          </v-flex>
          <v-flex d-flex md3 style="max-height:25%;">
            <BarChart            
            v-if="chartData['tExhC']"
             v-bind:chartOptions="chartData['tExhC']"/>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-flex d-flex md6>
        <v-layout column fill-height>
          <v-flex d-flex md3 style="max-height:25%;">
            <v-layout row>
                <v-flex d-flex md4>
                    <Card
                        v-bind:cardData="cardData['tcspeed']"
                        v-bind:flat="false"/>
                </v-flex>
                <v-flex d-flex md4>
                    <Card
                        v-bind:cardData="cardData['pscav']"
                        v-bind:flat="false"/>
                </v-flex>
                <v-flex d-flex md4>
                    <Card
                        v-bind:cardData="cardData['tscav']"
                        v-bind:flat="false"/>
                </v-flex>
            </v-layout>
          </v-flex>
          <v-flex d-flex md3 style="max-height:25%;">
            <v-layout row>
                <v-flex d-flex md4>
                    <Card
                        v-bind:cardData="cardData['tTurbIn']"
                        v-bind:flat="false"/>
                </v-flex>
                <v-flex d-flex md4>
                    <Card
                        v-bind:cardData="cardData['tTurbO']"
                        v-bind:flat="false"/>
                </v-flex>
                <v-flex d-flex md4>
                    <Card
                        v-bind:cardData="cardData['tliner']"
                        v-bind:flat="false"/>
                </v-flex>
            </v-layout>
          </v-flex>
          <v-flex d-flax md6 style="max-height:50%;">
            <v-layout row>
              <v-flex d-flex md4>
                <v-layout column fill-height>
                   <v-flex d-flex md6>
                      <Card
                          v-bind:cardData="cardData['fRailPres']"
                          v-bind:flat="false"/>
                    </v-flex>
                    <v-flex d-flex md6>
                      <Card
                          v-bind:cardData="cardData['soPresDispl']"
                          v-bind:flat="false"/>
                    </v-flex>
                </v-layout>
              </v-flex>              
              <v-flex d-flex md8>                
                <LoadDiagram/>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>


<script>

import Card from  '../controls/Card'
import BarChart from  '../controls/BarChart'
import LoadDiagram from  '../controls/LoadDiagram'

import {globalStore} from "../main.js"

export default {
  name: 'EngineParameters',
  components: {
    Card,
    BarChart,
    LoadDiagram
  },
  data () {
    return {
      cardData: {} ,
      chartData:{}
    }
  },
  mounted()
  {
    let cylCount=6;
    let tcCount=1;

    let cylParams=['pcomp', 'pmax'];   
    
    for (let cp=0;cp<cylParams.length;cp++)
    {
      let val = this.average(cylParams[cp], cylCount);

      this.$set(this.cardData,cylParams[cp],{
        value:val,
        ref:100,
        title:cylParams[cp],
        unit:'unit',
        format:2,
        color:0
      });
    }

    let tcParams=[ 'tcspeed', 'tscav','tTurbO','tTurbIn'];

    for (let tp=0;tp<tcParams.length;tp++)
    {
      let val = this.average(tcParams[tp], tcCount);

      this.$set(this.cardData,tcParams[tp],{
        value:val,
        ref:100,
        title:tcParams[tp],
        unit:'unit',        
        format:2,
        color:0
      });
    }

    let params = ['pscav','soPresDispl','fRailPres'];

    for (let p=0;p<params.length;p++)
    {
      let val = globalStore.data15m.sigs[params[p]];

      this.$set(this.cardData,params[p],{
        value:val,
        ref:100,
        title:params[p],
        unit:'unit',        
        format:2,
        color:0
      });
    } 
    
    let chartParams = ['pmax', 'pcomp', 'tExhC'];
    
    for (let p=0;p<params.length;p++)
    {
      this.$set(this.chartData, chartParams[p], {
        legend:{
          enabled:false
        },
        title:{
            text:chartParams[p]
        },
        series: [{                        
            type:'column',
            data:[]
        }]
      })

      for (let i=1;i<=cylCount;i++)
      {     
        let color = 'rgb(60, 171, 48)';

      // if ((val<min)||(val>max)) color = 'rgb(205, 57, 64)';

        this.chartData[chartParams[p]].series[0].data.push({
            x:i,
            y:globalStore.data15m.sigs[chartParams[p]+i],
            color:color
        });
          
      }
    }


    
  },
  methods: {
    average: function(param, count)
    {
      let sum =0;

      for (let i=1;i<=count;i++)
      {      
        sum+=globalStore.data15m.sigs[param+i];
      }

      return sum/count;
    }
  },
}
</script>

